/* App styles */
.App-logo {
  height: 100px;
  vertical-align: middle;
  pointer-events: none;
}

.topicList {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-end;
  
  margin: 0; 
  background: deepskyblue;
}

.topicList .topicGroup {
  text-align: left;
  font-weight: bold;
  display: block;
  padding: 5px;
  color: black;
}

.topicList .topicItem {
  text-align: right;
  display: block;
  padding: 5px;
  border: none;
  color: #497AA7;
  background-color: white;
}

.topicList .topicItem:hover {
  color: #133C5D !important;
  font-weight: bold;
}

.playbook {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  
  margin: 0; 
  background: white;
}

.playbook > div {
  flex: 1 100%;
  text-align: left;
  font-weight: normal;
  font-size: 0.7em;
  display: block;
  padding: 5px;
  color: black;
  min-height: 110px;
}

.playbook .title {
  text-align: center;
  font-weight: bold;
  font-size: 2em;
  padding: 0;

}

.playbook .sectionProblem {  
  border: 1px solid #5B9BD5;
  background: #9DBEE5;
  overflow-y: scroll;
  max-height: 300px;
}

.playbook .sectionQuestion {
  border: 1px solid #ED7D31;
  background: #F5B196;
}

.playbook .sectionVisualization {
  border: 1px solid #F6881F;
  background: white;
}

.playbook .sectionAction {
  border: 1px solid #70AD47;
  background: #FFD992;
}

.playbook .sectionValueProjection {
  border: 1px solid #70AD47;
  background: #B1D2A2;
}

/* Layout styles */
.wrapper {
  display: flex;  
  flex-flow: row wrap;
  font-weight: bold;
  text-align: center;
}

.wrapper > * {
  padding: 10px;
  flex: 1 100%;
}

.header {
  background: white;
}

.opportunity {
  background: lightskyblue;
}

.footer {
  background: white;
}

.main {
  text-align: center;
  background: white;
}

.aside-1 {
  background: white;
  border: 2px solid #41719C;
  padding: 5px;
}

.aside-2 {
  background: white;
  border: 2px solid #41719C;
  padding: 5px;
}

@media all and (min-width: 600px) {
  .aside { flex: 1 0 0; }
}

@media all and (min-width: 800px) {
  .main    { flex: 2 0px; }
  .aside-1 { order: 1; } 
  .main    { order: 2; }
  .aside-2 { order: 3; }
  .opportunity  { order: 4; }
  .footer  { order: 5; }
}

.navigation {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  
  list-style: none;
  margin: 0; 
  background: deepskyblue;
}

.navigation a {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: white;
}

.navigation a:hover {
  background: #1565C0;
}

@media all and (max-width: 800px) {
  .navigation {
    justify-content: space-around;
  }
}

@media all and (max-width: 600px) {
  .navigation {
    flex-flow: column wrap;
    padding: 0;
  }
  .navigation a { 
    text-align: center; 
    padding: 10px;
    border-top: 1px solid rgba(255, 255, 255,0.3); 
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); 
  }
  .navigation li:last-of-type a {
    border-bottom: none;
  }
}

body {
  padding: 2em; 
}
