.input-color {
    position: relative;
    margin-top: 10px;
}
.input-color input {
    padding-left: 20px;
    border: none; 
    border-width: 0; 
    box-shadow: none;
    background-color: transparent;
}
.input-color .color-box {
    width: 10px;
    height: 12px;
    display: inline-block;
    position: absolute;
    left: 5px;
    top: 5px;
}
.div-svg {
    text-align: center;        
    width: 100% !important; 
    float:left;
}
.div-color {
    margin-left: 10px; 
    width: 18% !important; 
    float:right;
}

g g path {
    fill: none;
    stroke: steelblue; 
    stroke-width: 1px;
    opacity: .8;
}
